import React, { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { Eye, EyeOff, User, Mail, Phone, MapPin } from 'lucide-react'
import { authService } from '../../services/authService'
import toast from 'react-hot-toast'

const RegisterPage = () => {
  const navigate = useNavigate()
  const [showPassword, setShowPassword] = useState(false)
  const [showConfirmPassword, setShowConfirmPassword] = useState(false)
  const [loading, setLoading] = useState(false)
  const [formData, setFormData] = useState({
    cccd: '',
    ho: '',
    ten: '',
    sdt: '',
    email: '',
    diaChi: '',
    maSoThue: '',
    matKhau: '',
    confirmPassword: ''
  })
  const [errors, setErrors] = useState({})

  const handleChange = (e) => {
    const { name, value } = e.target
    setFormData(prev => ({
      ...prev,
      [name]: value
    }))
    // Clear error when user starts typing
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }))
    }
  }

  const validateForm = () => {
    const newErrors = {}

    // Required fields
    if (!formData.cccd.trim()) newErrors.cccd = 'CCCD/CMND l bt buc'
    if (!formData.ho.trim()) newErrors.ho = 'H l bt buc'
    if (!formData.ten.trim()) newErrors.ten = 'Tn l bt buc'
    if (!formData.email.trim()) newErrors.email = 'Email l bt buc'
    if (!formData.sdt.trim()) newErrors.sdt = 'S in thoi l bt buc'
    if (!formData.matKhau) newErrors.matKhau = 'Mt khu l bt buc'
    if (!formData.confirmPassword) newErrors.confirmPassword = 'Xc nhn mt khu l bt buc'

    // CCCD validation
    const cccdRegex = /^[0-9]{9,12}$/
    if (formData.cccd && !cccdRegex.test(formData.cccd)) {
      newErrors.cccd = 'CCCD/CMND phi c 9-12 ch s'
    }

    // Email validation
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
    if (formData.email && !emailRegex.test(formData.email)) {
      newErrors.email = 'Email khng hp l'
    }

    // Phone validation
    const phoneRegex = /^[0-9]{10,11}$/
    if (formData.sdt && !phoneRegex.test(formData.sdt)) {
      newErrors.sdt = 'S in thoi phi c 10-11 ch s'
    }

    // Password validation
    if (formData.matKhau && formData.matKhau.length < 6) {
      newErrors.matKhau = 'Mt khu phi c t nht 6 k t'
    }

    // Confirm password validation
    if (formData.matKhau !== formData.confirmPassword) {
      newErrors.confirmPassword = 'Mt khu xc nhn khng khp'
    }

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  const handleSubmit = async (e) => {
    e.preventDefault()

    if (!validateForm()) {
      return
    }

    setLoading(true)
    try {
      // Remove confirmPassword from data sent to API
      const { confirmPassword, ...registerData } = formData

      const response = await authService.register(registerData)

      toast.success('ng k thnh cng! Vui lng ng nhp.')
      navigate('/login')
    } catch (error) {
      console.error('Registration error:', error)
      toast.error(error.message || 'C li xy ra khi ng k')
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-50 py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-md w-full space-y-8">
        <div>
          <div className="flex justify-center">
            <div className="w-12 h-12 bg-primary-600 rounded-lg flex items-center justify-center">
              <span className="text-white font-bold text-xl">H</span>
            </div>
          </div>
          <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-900">
            ng k ti khon
          </h2>
          <p className="mt-2 text-center text-sm text-gray-600">
            Hoc{' '}
            <Link
              to="/login"
              className="font-medium text-primary-600 hover:text-primary-500"
            >
              ng nhp nu  c ti khon
            </Link>
          </p>
        </div>

        <form className="mt-8 space-y-6" onSubmit={handleSubmit}>
          <div className="space-y-4">
            {/* CCCD Field */}
            <div>
              <label htmlFor="cccd" className="block text-sm font-medium text-gray-700">
                CCCD/CMND *
              </label>
              <div className="mt-1 relative">
                <input
                  id="cccd"
                  name="cccd"
                  type="text"
                  value={formData.cccd}
                  onChange={handleChange}
                  className={`input ${errors.cccd ? 'border-red-500' : ''}`}
                  placeholder="Nhp s CCCD/CMND"
                />
                <User className="absolute right-3 top-3 h-4 w-4 text-gray-400" />
              </div>
              {errors.cccd && <p className="mt-1 text-sm text-red-600">{errors.cccd}</p>}
            </div>

            {/* Name Fields */}
            <div className="grid grid-cols-2 gap-4">
              <div>
                <label htmlFor="ho" className="block text-sm font-medium text-gray-700">
                  H *
                </label>
                <div className="mt-1 relative">
                  <input
                    id="ho"
                    name="ho"
                    type="text"
                    value={formData.ho}
                    onChange={handleChange}
                    className={`input ${errors.ho ? 'border-red-500' : ''}`}
                    placeholder="Nhp h"
                  />
                  <User className="absolute right-3 top-3 h-4 w-4 text-gray-400" />
                </div>
                {errors.ho && <p className="mt-1 text-sm text-red-600">{errors.ho}</p>}
              </div>

              <div>
                <label htmlFor="ten" className="block text-sm font-medium text-gray-700">
                  Tn *
                </label>
                <div className="mt-1 relative">
                  <input
                    id="ten"
                    name="ten"
                    type="text"
                    value={formData.ten}
                    onChange={handleChange}
                    className={`input ${errors.ten ? 'border-red-500' : ''}`}
                    placeholder="Nhp tn"
                  />
                  <User className="absolute right-3 top-3 h-4 w-4 text-gray-400" />
                </div>
                {errors.ten && <p className="mt-1 text-sm text-red-600">{errors.ten}</p>}
              </div>
            </div>

            {/* Email */}
            <div>
              <label htmlFor="email" className="block text-sm font-medium text-gray-700">
                Email *
              </label>
              <div className="mt-1 relative">
                <input
                  id="email"
                  name="email"
                  type="email"
                  value={formData.email}
                  onChange={handleChange}
                  className={`input ${errors.email ? 'border-red-500' : ''}`}
                  placeholder="Nhp email"
                />
                <Mail className="absolute right-3 top-3 h-4 w-4 text-gray-400" />
              </div>
              {errors.email && <p className="mt-1 text-sm text-red-600">{errors.email}</p>}
            </div>

            {/* Phone */}
            <div>
              <label htmlFor="sdt" className="block text-sm font-medium text-gray-700">
                S in thoi *
              </label>
              <div className="mt-1 relative">
                <input
                  id="sdt"
                  name="sdt"
                  type="tel"
                  value={formData.sdt}
                  onChange={handleChange}
                  className={`input ${errors.sdt ? 'border-red-500' : ''}`}
                  placeholder="Nhp s in thoi"
                />
                <Phone className="absolute right-3 top-3 h-4 w-4 text-gray-400" />
              </div>
              {errors.sdt && <p className="mt-1 text-sm text-red-600">{errors.sdt}</p>}
            </div>

            {/* Address */}
            <div>
              <label htmlFor="diaChi" className="block text-sm font-medium text-gray-700">
                a ch
              </label>
              <div className="mt-1 relative">
                <input
                  id="diaChi"
                  name="diaChi"
                  type="text"
                  value={formData.diaChi}
                  onChange={handleChange}
                  className="input"
                  placeholder="Nhp a ch"
                />
                <MapPin className="absolute right-3 top-3 h-4 w-4 text-gray-400" />
              </div>
            </div>

            {/* Tax Code */}
            <div>
              <label htmlFor="maSoThue" className="block text-sm font-medium text-gray-700">
                M s thu
              </label>
              <div className="mt-1 relative">
                <input
                  id="maSoThue"
                  name="maSoThue"
                  type="text"
                  value={formData.maSoThue}
                  onChange={handleChange}
                  className="input"
                  placeholder="Nhp m s thu (ty chn)"
                />
                <User className="absolute right-3 top-3 h-4 w-4 text-gray-400" />
              </div>
            </div>



            {/* Password */}
            <div>
              <label htmlFor="matKhau" className="block text-sm font-medium text-gray-700">
                Mt khu *
              </label>
              <div className="mt-1 relative">
                <input
                  id="matKhau"
                  name="matKhau"
                  type={showPassword ? 'text' : 'password'}
                  value={formData.matKhau}
                  onChange={handleChange}
                  className={`input ${errors.matKhau ? 'border-red-500' : ''}`}
                  placeholder="Nhp mt khu"
                />
                <button
                  type="button"
                  className="absolute right-3 top-3 h-4 w-4 text-gray-400"
                  onClick={() => setShowPassword(!showPassword)}
                >
                  {showPassword ? <EyeOff /> : <Eye />}
                </button>
              </div>
              {errors.matKhau && <p className="mt-1 text-sm text-red-600">{errors.matKhau}</p>}
            </div>

            {/* Confirm Password */}
            <div>
              <label htmlFor="confirmPassword" className="block text-sm font-medium text-gray-700">
                Xc nhn mt khu *
              </label>
              <div className="mt-1 relative">
                <input
                  id="confirmPassword"
                  name="confirmPassword"
                  type={showConfirmPassword ? 'text' : 'password'}
                  value={formData.confirmPassword}
                  onChange={handleChange}
                  className={`input ${errors.confirmPassword ? 'border-red-500' : ''}`}
                  placeholder="Nhp li mt khu"
                />
                <button
                  type="button"
                  className="absolute right-3 top-3 h-4 w-4 text-gray-400"
                  onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                >
                  {showConfirmPassword ? <EyeOff /> : <Eye />}
                </button>
              </div>
              {errors.confirmPassword && <p className="mt-1 text-sm text-red-600">{errors.confirmPassword}</p>}
            </div>
          </div>

          {/* Submit Button */}
          <div>
            <button
              type="submit"
              disabled={loading}
              className="group relative w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-primary-600 hover:bg-primary-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-500 disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {loading ? (
                <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
              ) : null}
              {loading ? 'ang ng k...' : 'ng k'}
            </button>
          </div>

          {/* Terms */}
          <div className="text-center">
            <p className="text-xs text-gray-600">
              Bng vic ng k, bn ng  vi{' '}
              <Link to="/terms" className="text-primary-600 hover:text-primary-500">
                iu khon dch v
              </Link>{' '}
              v{' '}
              <Link to="/privacy" className="text-primary-600 hover:text-primary-500">
                Chnh sch bo mt
              </Link>{' '}
              ca chng ti.
            </p>
          </div>
        </form>
      </div>
    </div>
  )
}

export default RegisterPage
