import React, { useState } from 'react'
import { FileText, CreditCard, Banknote, QrCode, CheckCircle } from 'lucide-react'
import toast from 'react-hot-toast'

const InvoiceComponent = ({ 
  selectedGuest, 
  bill, 
  checkOutData, 
  onCheckOut, 
  onCancel, 
  loading 
}) => {
  const [showQRCode, setShowQRCode] = useState(false)
  const [paymentConfirmed, setPaymentConfirmed] = useState(false)

  // Tnh ton s tin cn thanh ton
  const remainingAmount = Math.max(0, bill.total - (bill.paidAmount || 0) - (selectedGuest?.depositAmount || 0))

  // To QR code cho chuyn khon
  const generateQRCode = () => {
    const amount = remainingAmount
    const addInfo = `Thanh toan hoa don ${selectedGuest?.maPhieuThue || ''}`
    return `https://img.vietqr.io/image/MB-0818181948-compact2.png?amount=${amount}&addInfo=${encodeURIComponent(addInfo)}`
  }




  // X l checkout theo phng thc thanh ton
  const handlePaymentMethodCheckout = () => {
    if (checkOutData?.paymentMethod === 'transfer') {
      if (!paymentConfirmed) {
        toast.error('Vui lng xc nhn  nhn tin chuyn khon')
        return
      }
    }
    onCheckOut()
  }

  return (
    <div className="space-y-6">
      {/* Thng tin ha n */}
      <div className="bg-blue-50 p-4 rounded-lg">
        <div className="flex items-center mb-3">
          <FileText className="w-5 h-5 text-blue-600 mr-2" />
          <h3 className="font-semibold text-gray-900">Ha n thanh ton</h3>
        </div>
        
        <div className="space-y-2 text-sm">
          <div className="flex justify-between">
            <span>Tin phng:</span>
            <span>{bill.roomCharges?.toLocaleString('vi-VN')} VN</span>
          </div>
          <div className="flex justify-between">
            <span>Dch v:</span>
            <span>{bill.serviceCharges?.toLocaleString('vi-VN')} VN</span>
          </div>
          <div className="flex justify-between">
            <span>Ph thu:</span>
            <span>{(bill.surcharges || 0)?.toLocaleString('vi-VN')} VN</span>
          </div>
          <div className="border-t pt-2 flex justify-between font-semibold text-lg">
            <span>Tng cng:</span>
            <span>{bill.total?.toLocaleString('vi-VN')} VN</span>
          </div>

          {/* Payment Details */}
          <div className="border-t pt-2 space-y-1">
            <div className="flex justify-between text-green-600">
              <span> thanh ton:</span>
              <span>{(bill.paidAmount || 0)?.toLocaleString('vi-VN')} VN</span>
            </div>
            <div className="flex justify-between text-green-600">
              <span>Tin t cc:</span>
              <span>{(selectedGuest?.depositAmount || 0)?.toLocaleString('vi-VN')} VN</span>
            </div>
            <div className="border-t pt-2 flex justify-between font-bold text-red-600 text-lg">
              <span>S tin phi tr:</span>
              <span>{remainingAmount?.toLocaleString('vi-VN')} VN</span>
            </div>
          </div>
        </div>
      </div>

      {/* Phng thc thanh ton */}
      <div className="bg-gray-50 p-4 rounded-lg">
        <h4 className="font-semibold text-gray-900 mb-3">Phng thc thanh ton</h4>
        
        {checkOutData?.paymentMethod === 'cash' && (
          <div className="flex items-center p-3 bg-green-50 border border-green-200 rounded-lg">
            <Banknote className="w-5 h-5 text-green-600 mr-2" />
            <div>
              <p className="font-medium text-green-800">Thanh ton tin mt</p>
              <p className="text-sm text-green-600">
                Khch hng cn thanh ton: <span className="font-bold">{remainingAmount?.toLocaleString('vi-VN')} VN</span>
              </p>
            </div>
          </div>
        )}

        {checkOutData?.paymentMethod === 'transfer' && (
          <div className="space-y-3">
            <div className="flex items-center p-3 bg-blue-50 border border-blue-200 rounded-lg">
              <CreditCard className="w-5 h-5 text-blue-600 mr-2" />
              <div className="flex-1">
                <p className="font-medium text-blue-800">Thanh ton chuyn khon</p>
                <p className="text-sm text-blue-600">
                  S tin: <span className="font-bold">{remainingAmount?.toLocaleString('vi-VN')} VN</span>
                </p>
              </div>
              <button
                onClick={() => setShowQRCode(!showQRCode)}
                className="flex items-center px-3 py-1 bg-blue-500 text-white text-sm rounded hover:bg-blue-600 transition-colors"
              >
                <QrCode className="w-4 h-4 mr-1" />
                {showQRCode ? 'n QR' : 'Hin QR'}
              </button>
            </div>

            {showQRCode && (
              <div className="text-center p-4 bg-white border rounded-lg">
                <p className="text-sm text-gray-600 mb-3">Qut m QR  chuyn khon</p>
                <img 
                  src={generateQRCode()} 
                  alt="QR Code chuyn khon" 
                  className="mx-auto mb-3 max-w-xs"
                />
                <div className="text-xs text-gray-500 space-y-1">
                  <p>Ngn hng: MB Bank</p>
                  <p>S TK: 0818181948</p>
                  <p>S tin: {remainingAmount?.toLocaleString('vi-VN')} VN</p>
                  <p>Ni dung: Thanh ton ha n {selectedGuest?.maPhieuThue}</p>
                </div>
              </div>
            )}

            {/* Xc nhn  nhn tin */}
            <div className="p-3 bg-yellow-50 border border-yellow-200 rounded-lg">
              <label className="flex items-center">
                <input
                  type="checkbox"
                  checked={paymentConfirmed}
                  onChange={(e) => setPaymentConfirmed(e.target.checked)}
                  className="mr-2"
                />
                <span className="text-sm text-yellow-800">
                  Xc nhn  nhn tin chuyn khon t khch hng
                </span>
              </label>
            </div>
          </div>
        )}
      </div>

      {/* Buttons */}
      <div className="flex space-x-3">
        <button
          onClick={onCancel}
          className="btn-outline flex-1"
        >
          Hy
        </button>
        <button
          onClick={handlePaymentMethodCheckout}
          disabled={loading || !checkOutData?.actualCheckOut || (checkOutData?.paymentMethod === 'transfer' && !paymentConfirmed)}
          className="btn-primary flex-1"
        >
          {loading ? (
            <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
          ) : null}
          Hon tt Check-out
        </button>
      </div>
    </div>
  )
}

export default InvoiceComponent
