import React, { useState, useEffect } from 'react';
import { Phone, User, Mail, AlertCircle } from 'lucide-react';
import { useAuth } from '../../contexts/AuthContext';

const ContactInfo = ({ bookingData, setBookingData, onNext, onBack }) => {
  const { user } = useAuth();

  const [customerInfo, setCustomerInfo] = useState({
    fullName: '',
    phone: '',
    email: '',
    idCard: ''
  });

  const [errors, setErrors] = useState({});

  // T ng in thng tin t ti khon  ng nhp
  useEffect(() => {
    if (user) {
      console.log('User data for auto-fill:', user); // Debug log

      setCustomerInfo({
        fullName: user.hoTen || user.tenKhachHang || user.fullName || user.name || '',
        phone: user.sdt || user.soDienThoai || user.phone || user.phoneNumber || '',
        email: user.email || '',
        idCard: user.cccd || user.cmnd || user.idCard || user.identityCard || ''
      });
    }
  }, [user]);

  const validateForm = () => {
    const newErrors = {};

    if (!customerInfo.fullName.trim()) {
      newErrors.fullName = 'Vui lng nhp h tn';
    }

    if (!customerInfo.phone.trim()) {
      newErrors.phone = 'Vui lng nhp s in thoi';
    } else if (!/^[0-9]{10,11}$/.test(customerInfo.phone)) {
      newErrors.phone = 'S in thoi khng hp l';
    }

    if (!customerInfo.email.trim()) {
      newErrors.email = 'Vui lng nhp email';
    } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(customerInfo.email)) {
      newErrors.email = 'Email khng hp l';
    }

    if (!customerInfo.idCard.trim()) {
      newErrors.idCard = 'Vui lng nhp CCCD/CMND';
    } else if (!/^[0-9]{9,12}$/.test(customerInfo.idCard)) {
      newErrors.idCard = 'CCCD/CMND khng hp l';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleInputChange = (field, value) => {
    setCustomerInfo(prev => ({
      ...prev,
      [field]: value
    }));

    // Clear error when user starts typing
    if (errors[field]) {
      setErrors(prev => ({
        ...prev,
        [field]: ''
      }));
    }
  };

  const handleContinue = () => {
    if (validateForm()) {
      setBookingData({
        ...bookingData,
        customerInfo
      });
      onNext();
    }
  };

  const formatPrice = (price) => {
    return new Intl.NumberFormat('vi-VN', {
      style: 'currency',
      currency: 'VND'
    }).format(price);
  };

  return (
    <div className="space-y-6">
      {/* Hotel Contact Info */}
      <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-6">
        <div className="flex items-start space-x-3">
          <AlertCircle className="text-yellow-600 mt-1" size={20} />
          <div>
            <h3 className="font-semibold text-yellow-800 mb-2">Thng tin lin lc khch sn</h3>
            <div className="space-y-2 text-yellow-700">
              <div className="flex items-center space-x-2">
                <Phone size={16} />
                <span className="font-medium">Hotline: 1900-1234</span>
              </div>
              <p className="text-sm">
                <strong>Lu  quan trng:</strong> Sau khi in thng tin, vui lng ch  nghe my.
                Nhn vin khch sn s gi  xc nhn t phng v hng dn thanh ton trong vng 15 pht.
              </p>
            </div>
          </div>
        </div>
      </div>

      {/* Booking Summary */}
      <div className="bg-gray-50 rounded-lg p-4">
        <h4 className="font-semibold mb-3">Tm tt t phng</h4>
        <div className="grid grid-cols-2 gap-4 text-sm">
          <div>
            <span className="text-gray-600">Phng:</span>
            <span className="ml-2 font-medium">{bookingData.room.tenKieuPhong} - {bookingData.room.tenLoaiPhong}</span>
          </div>
          <div>
            <span className="text-gray-600">S lng:</span>
            <span className="ml-2 font-medium">{bookingData.roomQuantity || 1} phng</span>
          </div>
          <div>
            <span className="text-gray-600">S m:</span>
            <span className="ml-2 font-medium">{bookingData.nights} m</span>
          </div>
          <div>
            <span className="text-gray-600">Nhn phng:</span>
            <span className="ml-2 font-medium">{bookingData.checkIn?.toLocaleDateString('vi-VN')}</span>
          </div>
          <div>
            <span className="text-gray-600">Tr phng:</span>
            <span className="ml-2 font-medium">{bookingData.checkOut?.toLocaleDateString('vi-VN')}</span>
          </div>
          <div>
            <span className="text-gray-600">Tng tin:</span>
            <span className="ml-2 font-medium text-blue-600">{formatPrice(bookingData.totalAmount)}</span>
          </div>
          <div>
            <span className="text-gray-600">t cc:</span>
            <span className="ml-2 font-medium text-orange-600">{formatPrice(bookingData.depositAmount)}</span>
          </div>
        </div>
      </div>

      {/* Customer Information Form */}
      <div className="space-y-4">
        <div className="flex justify-between items-center">
          <h3 className="text-lg font-semibold">Thng tin khch hng</h3>
          {user && (
            <div className="text-sm text-blue-600 bg-blue-50 px-3 py-1 rounded-full">
               in t ti khon ca bn
            </div>
          )}
        </div>


        
        <div className="grid md:grid-cols-2 gap-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              <User className="inline w-4 h-4 mr-1" />
              H v tn *
            </label>
            <input
              type="text"
              value={customerInfo.fullName}
              onChange={(e) => handleInputChange('fullName', e.target.value)}
              className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent ${
                errors.fullName ? 'border-red-500' : 'border-gray-300'
              }`}
              placeholder="Nhp h v tn"
            />
            {errors.fullName && <p className="text-red-500 text-sm mt-1">{errors.fullName}</p>}
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              <Phone className="inline w-4 h-4 mr-1" />
              S in thoi *
            </label>
            <input
              type="tel"
              value={customerInfo.phone}
              onChange={(e) => handleInputChange('phone', e.target.value)}
              className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent ${
                errors.phone ? 'border-red-500' : 'border-gray-300'
              }`}
              placeholder="Nhp s in thoi"
            />
            {errors.phone && <p className="text-red-500 text-sm mt-1">{errors.phone}</p>}
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              <Mail className="inline w-4 h-4 mr-1" />
              Email *
            </label>
            <input
              type="email"
              value={customerInfo.email}
              onChange={(e) => handleInputChange('email', e.target.value)}
              className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent ${
                errors.email ? 'border-red-500' : 'border-gray-300'
              }`}
              placeholder="Nhp email"
            />
            {errors.email && <p className="text-red-500 text-sm mt-1">{errors.email}</p>}
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              CCCD/CMND *
            </label>
            <input
              type="text"
              value={customerInfo.idCard}
              onChange={(e) => handleInputChange('idCard', e.target.value)}
              className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent ${
                errors.idCard ? 'border-red-500' : 'border-gray-300'
              }`}
              placeholder="Nhp s CCCD/CMND"
            />
            {errors.idCard && <p className="text-red-500 text-sm mt-1">{errors.idCard}</p>}
          </div>
        </div>
      </div>

      {/* Action Buttons */}
      <div className="flex justify-between pt-4">
        <button
          onClick={onBack}
          className="px-6 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors"
        >
          Quay li
        </button>
        <button
          onClick={handleContinue}
          className="px-6 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
        >
          Tip tc thanh ton
        </button>
      </div>
    </div>
  );
};

export default ContactInfo;
