// Convert VND to USD (t gi tm thi cho demo)
export const convertVNDToUSD = (vndAmount) => {
  const exchangeRate = 24000; // 1 USD = 24,000 VND (t gi tm thi)
  return Math.round((vndAmount / exchangeRate) * 100) / 100; // Lm trn 2 ch s thp phn
};

// Convert USD back to VND for display
export const convertUSDToVND = (usdAmount) => {
  const exchangeRate = 24000;
  return Math.round(usdAmount * exchangeRate);
};
